<template>
	<div id="index">
		
		<!-- //用来包含粒子的DOM元素-->
		<mu-flexbox style="width: 100%;height: 100%; text-align: center;position: fixed;top: 0;z-index: 100;pointer-events:none">
			<mu-flexbox-item class="flex-demo">
				<!--style="width: 150px;height: 200px;position: fixed;top: 50%;transform: translateY(-50%);left: 50%;translateX(-50%);"-->
				<div>
					<img src="../../build/logo.png" width="150px" />
					<div>
						<mu-raised-button label="Jiaqi" primary class="demo-raised-button" @click="focusInput" style="pointer-events:painted" />
					</div>
					<div>
						<mu-text-field label="回车发送弹幕" errorColor="white" v-model="inputValue" ref="input" v-show="true" @keyup.enter.native="push" labelFloat style="pointer-events:painted" /><br/>
					</div>
				</div>
			</mu-flexbox-item>
		</mu-flexbox>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				inputFlag: false,
				inputValue: ''
			}
		},
		created() { //立即执行

		},
		methods: {
			focusInput() {
				this.inputFlag = !this.inputFlag
				setTimeout(() => {
					this.$refs.input.focus()
				})
			},
			push() {
				var creSpan = $("<span class='string'>" + this.inputValue + "</span>");
				creSpan.css("top", Math.random() * window.innerHeight + "px") //随机颜色
				creSpan.css("color", "#" + (Math.random() * 1000000).toFixed(0)) //随机颜色
				creSpan.css("font-size", Math.random() * 24 + 30 + "px") //随机颜色
				$("#index").append(creSpan)
				this.inputValue = ''
				creSpan.animate({
					left: "-500px",
					opacity: 0
				}, 5000, function() {
					creSpan.remove()
				})
			},
		},
		mounted() { //挂载完成执行
			

		},

	}
</script>

<style>
	
	
	.string {
		z-index: 999;
		width: 300px;
		position: fixed;
		right: 0;
		color: white;
	}
	
	.mu-text-field .focus-state {
		color: white !important;
	}
</style>


