<template>
	<div class="Tab">
		<!--头部-->
		<div id="header" style="position: absolute;top: 0;z-index: 999;width: 100%;">
			<mu-appbar>
				<mu-icon-button slot="left" @click="toggle('drawer')"><i class="fa fa-bars" /></mu-icon-button>
				<mu-icon-button slot="right" ref="button" @click="toggle('theme')"><i class="fa fa-google-wallet" /></mu-icon-button>
				<mu-popover :trigger="trigger" :open="open" @close="handleClose">
					<mu-menu>
						<span v-for="item in theme_radio_list" v-if="theme_radio_list.length">
				    		<mu-radio @change="changeTheme" name="group" :nativeValue="item.nativeValue" v-model="radio_value" :label="item.lable" style="" :labelClass="item.style" :iconClass="item.style"/><br/>
				    	</span>
					</mu-menu>
				</mu-popover>
			</mu-appbar>
		</div>
		<mu-drawer :open="showDrawer" @close="toggle('drawer')" :docked="false">
			<mu-appbar style="height: 200px;" titleClass="titleClass">
				<mu-icon-button icon="" slot="left" />
				<div id="">
					<img src="../assets/logo.png" style="width: 100px;" />
					<div>
						Jiaqi
					</div>
				</div>
				<mu-flat-button label="" slot="right" />
				<mu-icon-button icon="" slot="right" />
			</mu-appbar>
			<mu-list @itemClick="itemClick" :value="listValue">
				<mu-list-item v-for="item in route" :title="item.name" style="text-align: left;" :key="item.name" :value="item.path">
					<i :class="item.icon" slot="left" style="font-size: 28px;margin-bottom: 5px;"></i>
				</mu-list-item>
			</mu-list>
		</mu-drawer>
		
		<transition name="slide-fade" mode="out-in">
			<keep-alive>
				<router-view style="margin-top: 64px;"/>
			</keep-alive>
		</transition>
		<div id="particles-js"></div>
	</div>
</template>

<script>
	import light from '!raw-loader!muse-ui/dist/theme-default.css'
	import dark from '!raw-loader!muse-ui/dist/theme-dark.css'
	import carbon from '!raw-loader!muse-ui/dist/theme-carbon.css'
	import teal from '!raw-loader!muse-ui/dist/theme-teal.css'
	export default {
		data() {
			return {
				theme: 'teal', //主题
				themes: {
					light,
					dark,
					carbon,
					teal
				},
				radio_value: 'teal', //颜色选择
				theme_radio_list: [{
						nativeValue: 'light',
						lable: 'LIGHT',
						style: "radio-light"
					}, {
						nativeValue: 'dark',
						lable: 'DARK',
						style: "radio-dark"
					},
					{
						nativeValue: 'carbon',
						lable: 'CARBON',
						style: "radio-carbon"
					}, {
						nativeValue: 'teal',
						lable: 'TEAL',
						style: "radio-teal"
					}
				],
				open: false,
				showDrawer: false,
				route: this.$router.options.routes[0].children,
				trigger: null,
				listValue: this.$route.path
			}
		},
		mounted() {
			this.trigger = this.$refs.button.$el
			this.drowParticles()
		},
		methods: {
			changeTheme(theme) {
				this.theme = theme
				const styleEl = this.getThemeStyle()
				styleEl.innerHTML = this.themes[theme] || ''
			},
			getThemeStyle() {
				const themeId = 'muse-theme'
				let styleEl = document.getElementById(themeId)
				if(styleEl) return styleEl
				styleEl = document.createElement('style')
				styleEl.id = themeId
				document.body.appendChild(styleEl)
				return styleEl
			},
			//  颜色选择、drawer菜单
			toggle(type) {
				console.log(this.$route)
				if(type == 'drawer') {
					this.showDrawer = !this.showDrawer
				} else if(type == 'theme') {
					this.open = !this.open
				}
			},
			handleClose(e) {
				this.open = false
			},
			itemClick(listItem) {
				this.listValue = listItem.value
				this.$router.push(this.listValue)
			},
			drowParticles() {
				var color ="#"+(Math.random()*1000000).toFixed(0)
				console.log(color)
				particlesJS('particles-js', {
					particles: {
						color: color  ,
						shape: 'circle', // "circle", "edge" or "triangle"
						opacity: 0.7,
						size: 5,
						size_random: true,
						nb: 80,
						line_linked: {
							enable_auto: true,
							distance: 90,
							color: color,
							opacity: 1,
							width: 1,
							condensed_mode: {
								enable: false,
								rotateX: 600,
								rotateY: 600
							}
						},
						anim: {
							enable: true,
							speed: 1
						}
					},
					interactivity: {
						enable: true,
						mouse: {
							distance: 300
						},
						detect_on: 'canvas', // "canvas" or "window"
						mode: 'grab',
						line_linked: {
							opacity: 1
						},
						events: {
							onclick: {
								enable: true,
								mode: 'push', // "push" or "remove"
								nb: 1
							}
						}
					},
					/* Retina Display Support */
					retina_detect: true
				});
			},
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	#particles-js {
		width: 100%;
		height: 100%;
		background: #f0f0f0;
		opacity: 0.7;
		/*// 背景色（不要设置为白色，因为粒子默认是白色）*/
		position: fixed;
		z-index: -1;
		top: 0;
		pointer-events:auto
	}
	h1,
	h2 {
		font-weight: normal;
	}
	
	ul {
		list-style-type: none;
		padding: 0;
	}
	
	li {
		display: inline-block;
		margin: 0 10px;
	}
	
	a {
		color: #42b983;
	}
	
	.radio-light {
		color: #673ab7 !important;
	}
	
	.radio-dark {
		color: black !important;
	}
	
	.radio-carbon {
		color: #607d8b !important;
	}
	
	.radio-teal {
		color: #009688 !important;
	}
	
	.titleClass {
		line-height: 1;
	}
	
	/* 可以设置不同的进入和离开动画 */
/* 设置持续时间和动画函数 */
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-active {
  padding-left: 10px;
  opacity: 0;
}
</style>


