import Vue from 'vue'
import Router from 'vue-router'
import Tab from '@/components/Tab'
import index from '@/page/index'
import chatRoom from '@/page/chatRoom'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Tab',
      component: Tab,
      children:[
      	{path:'/',name:'index',component:index,name:"主页",icon:'fa fa-home'},
      	{path:'/chatRoom',name:'chatRoom',component:chatRoom,name:"聊天室",icon:'fa fa-wechat'},
      ]
    },
    
  ]
})



